<template>

  <div class="section-container">
    <span class="title text-center">Hello</span>
        <hr />
    <div class="row-container">
      <div class="row align-items-center">
        <p v-html="description"></p>
        <div class="button-container">
          <button class="btn btn-outline-secondary" @click="open('linkedin')">
            LinkedIn
            <i class="fab fa-linkedin"></i>
          </button>
          <button class="btn btn-outline-secondary" @click="open('github')">
            GitHub
            <i class="fa-brands fa-github"></i>
          </button>
          <button class="btn btn-outline-secondary" @click="open('resume')">
            Resume
            <i class="fa fa-file"></i>
          </button>
          <button class="btn btn-outline-secondary" @click="open('contact')">
            Contact
            <i class="fa-regular fa-envelope"></i>
          </button>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
import '@fortawesome/fontawesome-free/css/all.min.css';
import info from "../../info";
let email, subject, body;
let url = '';

export default {
  name: "HomeSection",
  data() {
    return {
      picture: info.flat_picture,
      description: info.description,
      linkedin: info.links.linkedin,
      github: info.links.github,
      resume: info.links.resume
    };
  },
  methods: {
    open(link) {
      switch (link) {
        case "linkedin":
          window.open(this.linkedin, "_blank");
          break;
        case "github":
          window.open(this.github, "_blank");
          break;
        case "resume":
          window.open(this.resume, "_blank");
          break;
        case "contact":
          email = 'darrison@andrew.cmu.edu';
          subject = '';
          body = '';
          url = `mailto:${email}?subject=${encodeURIComponent(subject)}&body=${encodeURIComponent(body)}`;
          window.open(url, "_blank");
          break;
      }
    },
  },
};
</script>

<style scoped>
.section-container {
  background-color: var(--primary-bg-color);
}


.flex-container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
}

p {
  text-align: left !important;
}

