<template>
  <div>
    <header class="header">
      <div class="title1">Ollie.</div>
      <nav>
        <ul class="nav-list">
          <li><a @click.prevent="scrollToSection('home')">Home</a></li>
          <li><a @click.prevent="scrollToSection('about')">About</a></li>
          <li><a @click.prevent="scrollToSection('portfolio')">Portfolio</a></li>
          <li><a @click.prevent="scrollToSection('skills')">Skills</a></li>
          <li><a @click.prevent="scrollToSection('artwork')">Artwork</a></li>
          <!--<li><a @click.prevent="scrollToSection('contact')">Contact</a></li>-->
          <li class="more">
            <a href="#" @click.prevent="toggleDropdown">
              <i class="fa-solid fa-bars"></i>
            </a>
          </li>
        </ul>
      </nav>
    </header>
    <ul class="dropdown" v-show="dropdownVisible">
      <li><a @click.prevent="scrollToSection('home')">Home</a></li>
      <li><a @click.prevent="scrollToSection('about')">About</a></li>
      <li><a @click.prevent="scrollToSection('portfolio')">Portfolio</a></li>
      <li><a @click.prevent="scrollToSection('skills')">Skills</a></li>
      <li><a @click.prevent="scrollToSection('artwork')">Artwork</a></li>
      <!--<li><a @click.prevent="scrollToSection('contact')">Contact</a></li>-->
    </ul>
    <main>
      <section id="launch">
        <LaunchSection />
      </section>
      <section id="home">
        <HomeSection />
      </section>
      <section id="about">
        <AboutSection />
      </section>
      <section id="portfolio">
        <PortfolioSection />
      </section>
      <section id="skills">
        <SkillsSection />
      </section>
      <section id="artwork">
        <ArtworkSection />
      </section>
    </main>
    <footer>
      <div class="row-container">
        <div class="box">
          <div class="title3">&copy; 2025 Ollie Arrison. All rights reserved.</div>
        </div>
        <div class="box">
          <button class="btn btn-outline-secondary" @click="open('linkedin')">
            LinkedIn
            <i class="fab fa-linkedin"></i>
          </button>
          <button class="btn btn-outline-secondary" @click="open('github')">
            GitHub
            <i class="fa-brands fa-github"></i>
          </button>
          <button class="btn btn-outline-secondary" @click="open('resume')">
            Resume
            <i class="fa fa-file"></i>
          </button>
          <button class="btn btn-outline-secondary" @click="open('contact')">
            Contact
            <i class="fa-regular fa-envelope"></i>
          </button>
        </div>

      </div>
    </footer>
  </div>
</template>

<script>
import LaunchSection from "../components/main/Launch.vue";
import HomeSection from "../components/main/Home.vue";
import AboutSection from "../components/main/About.vue";
import PortfolioSection from "../components/main/Portfolio.vue";
import SkillsSection from "../components/main/Skills.vue";
import ArtworkSection from "../components/main/Artwork.vue";
//import ContactSection from "../components/main/Contact.vue";
//import ComputerGraphicsSection from "../components/main/Graphics.vue";
import info from "../info";

let email, subject, body;
let url = '';

export default {
  name: "App",
  data() {
    return {
      dropdownVisible: false,
    };
  },
  components: {
    LaunchSection,
    HomeSection,
    AboutSection,
    //ComputerGraphicsSection,
    PortfolioSection,
    SkillsSection,
    ArtworkSection,
    //ContactSection,
  },
  methods: {
    open(link) {
      switch (link) {
        case "linkedin":
          window.open(info.links.linkedin, "_blank");
          break;
        case "github":
          window.open(info.links.github, "_blank");
          break;
        case "resume":
          window.open(info.links.resume, "_blank");
          break;
        case "contact":
          email = 'darrison@andrew.cmu.edu';
          subject = '';
          body = '';
          url = `mailto:${email}?subject=${encodeURIComponent(subject)}&body=${encodeURIComponent(body)}`;
          window.open(url, "_blank");
          break;
      }
    },
    scrollToSection(sectionId) {
      const element = document.getElementById(sectionId);
      if (element) {
        element.scrollIntoView({ behavior: "smooth" });
      }
    },
    toggleDropdown() {
      this.dropdownVisible = !this.dropdownVisible;
    },
    handleClickOutside(event) {
      if (!this.$el.contains(event.target) && this.dropdownVisible) {
        this.dropdownVisible = false;
      }
    },
  },
  mounted() {
    document.addEventListener("click", this.handleClickOutside);
  },
  beforeUnmount() {
    document.removeEventListener("click", this.handleClickOutside);
  },
};
</script>

