        <i class="fa-solid fa-chevron-down" :class="caretIconClass" @click="toggleContent"></i>
        <hr v-if="showContent" />
        <div v-if="showContent">
            <div class="px-2 title2">In my spare time, I enjoy creating digital art using Procreate and Blender.</div>
            <div class="row-container">
                <div class="box" v-for="(s, index) in art" :key="index">
                    <img class="card-img-top" :src="s" alt="Screenshot of project" />
                </div>
            </div>
        </div>
    </div>
</template>

