import info from "../../info";

export default {
    name: 'ArtworkSection',
    data() {
        return {
            art: info.artwork,
            showContent: false,
        };
    },
    computed: {
        caretIconClass() {
            return {
                'fa-solid fa-chevron-down': true,
                'active': this.showContent,
            };
