<template>
  <div class="section-container">
    <div class="float-container">
      <button @click="open('github')" class="btn btn-outline-secondary">
        About Cube
        <i class="fa-brands fa-github"></i>

      </button>



      <!-- Intro toggle button -->
      <button @click="toggleIntro" class="btn btn-outline-secondary" :aria-pressed="showIntro.toString()"
        aria-controls="intro-block">
        
        <i class="fa-solid fa-eye" v-if="showIntro"></i>
        <i class="fa-solid fa-eye-slash" v-else></i>
      </button>

    </div>

    <!-- Toggle intro block -->

    <div class="flex-container">

      <div class="line" id="intro-block" v-show="showIntro">

        <div class="row">

          <span class="main-title">Hello, I'm Ollie.</span>

          <div class="title2">Computer Science Student</div>

        </div>

      </div>

    </div>



    <div class="space"></div>



    <button @click="scrollToSection('home')" data-tooltip="GitHub">

      <i class="fa-solid fa-arrow-down"></i>

    </button>

  </div>

</template>



<script>

import * as THREE from 'three';

import '@fortawesome/fontawesome-free/css/all.min.css';

import info from "../../info";



export default {

  name: "LaunchSection",

  data() {

    return {

      picture: info.flat_picture,

      description: info.description,

      linkedin: info.links.linkedin,

      github: info.links.github,

      resume: info.links.resume,

      scene: null,

      camera: null,

      renderer: null,

      nebula: null,

      showIntro: true

    };

  },

  methods: {

    scrollToSection(sectionId) {

      const element = document.getElementById(sectionId);

      if (element) element.scrollIntoView({ behavior: "smooth" });

    },

    open(link) {

      switch (link) {

        case "linkedin": window.open(this.linkedin, "_blank"); break;

        case "github": window.open("https://github.com/olliearrison/Cube/tree/main", "_blank"); break;

        case "resume": window.open(this.resume, "_blank"); break;

      }

    },

    toggleIntro() {

      this.showIntro = !this.showIntro;

    },

  },

  mounted() {

    const scene = new THREE.Scene();

    const camera = new THREE.PerspectiveCamera(27, window.innerWidth / window.innerHeight, 5, 3500);

    camera.position.z = 3250;

    const renderer = new THREE.WebGLRenderer({ alpha: true });

    const canvas = renderer.domElement;

    renderer.setSize(window.innerWidth, window.innerHeight);

    renderer.setPixelRatio(window.devicePixelRatio);

    renderer.setAnimationLoop(animate);

    document.body.appendChild(renderer.domElement);

    let points;



    scene.background = new THREE.Color(0x6f7691);

    scene.fog = new THREE.Fog(0x6f7691, 2000, 3500);



    const particles = 100000;

    const g = new THREE.BufferGeometry();

    const positions = [];

    const colors = [];

    const color = new THREE.Color();

    const n = 800, n2 = n / 2;



    for (let i = 0; i < particles; i++) {

      const x = Math.random() * n - n2;

      const y = Math.random() * n - n2;

      const z = Math.random() * n - n2;

      positions.push(x, y, z);

      const vx = (x / n) + 0.5;

      const vy = (y / n) + 0.5;

      const vz = (z / n) + 0.5;

      color.setRGB(vx, vy, vz);

      colors.push(color.r, color.g, color.b);

    }



    g.setAttribute('position', new THREE.Float32BufferAttribute(positions, 3));

    g.setAttribute('color', new THREE.Float32BufferAttribute(colors, 3));

    g.computeBoundingSphere();



    const m = new THREE.PointsMaterial({ size: 35, vertexColors: true });

    points = new THREE.Points(g, m);

    scene.add(points);



    const geometry = new THREE.BoxGeometry(1, 1, 1);

    const material = new THREE.MeshBasicMaterial({ color: 0x00ff00 });

    const cube = new THREE.Mesh(geometry, material);

    scene.add(cube);



    function onWindowResize() {

      camera.aspect = window.innerWidth / window.innerHeight;

      camera.updateProjectionMatrix();

      renderer.setSize(window.innerWidth, window.innerHeight);

      mouse.x = (lastMouseX / window.innerWidth) * 2 - 1;

      mouse.y = -(lastMouseY / window.innerHeight) * 2 + 1;

    }

    window.addEventListener('resize', onWindowResize);



    // Set to a value that will be off the cube in world-space

    const mouse = new THREE.Vector2(-100, -100);

    const raycaster = new THREE.Raycaster();

    let lastMouseX = 0;

    let lastMouseY = 0;



    window.addEventListener('mousemove', (event) => {

      const rect = canvas.getBoundingClientRect();

      lastMouseX = mouse.x;

      lastMouseY = mouse.y;

      mouse.x = ((event.clientX - rect.left) / rect.width) * 2 - 1;

      mouse.y = -((event.clientY - rect.top) / rect.height) * 2 + 1;

    });



    function animate() {

      const time = Date.now() * 0.0005;

      points.rotation.x = time * 0.25;

      points.rotation.y = time * 0.5;

      raycaster.setFromCamera(mouse, camera);

      raycaster.params.Points.threshold = 25;

      const intersects = raycaster.intersectObject(points, false);

      for (let i = 0; intersects.length > i; i++) {

        const index = intersects[i].index;

        const colorAttr = g.getAttribute('color');

        colorAttr.array[index * 3 + 0] *= 1.10;

        colorAttr.array[index * 3 + 1] *= 1.05;

        colorAttr.array[index * 3 + 2] *= 1.04;

        colorAttr.needsUpdate = true;

      }

      renderer.render(scene, camera);

    }



    renderer.domElement.style.position = 'absolute';

    renderer.domElement.style.top = '0';

    renderer.domElement.style.left = '0';

    renderer.domElement.style.zIndex = '-1';

  }

};

</script>



