import { createApp } from 'vue'
import App from './App.vue'

import VTooltip from 'v-tooltip';

// Vuetify
import 'vuetify/styles'
import { createVuetify } from 'vuetify'
import * as components from 'vuetify/components'
import * as directives from 'vuetify/directives'
import '@mdi/font/css/materialdesignicons.css'

// Vue Router
import { createRouter, createWebHistory } from 'vue-router'
import HomePage from './views/HomePage.vue'
import SuraDemo from './views/SuraDemo.vue'
import LifeDemo from './views/LifeDemo.vue'

const routes = [
  { path: '/', component: HomePage },
  { path: '/sura-demo', component: SuraDemo },
  { path: '/game-of-life', component: LifeDemo },
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

const vuetify = createVuetify({
  components,
  directives,
  theme: {
    defaultTheme: 'light',
    themes: {
      light: {
        'light-color': '#FFFFFF', // Set your custom color here
      },
      dark: {
        'light-color': '#FFFFFF', // Set your custom color for dark mode
      }
    },
  }
})

createApp(App).use(vuetify).use(VTooltip).use(router).mount('#app')
