<template>
  <div class="card">
    <div style="height: 180px;">
      <img class="card-img-top" :src="portfolio.portfolio.picture" alt="Screenshot of project" />
    </div>
    <div class="px-2 title2">{{ portfolio.portfolio.name }}</div>
    <div class="px-2 date">{{ portfolio.portfolio.date }}</div>

    <p>{{ portfolio.portfolio.description }}</p>

    <span class="mx-2 badge p-2 mb-2" v-for="s in portfolio.portfolio.skills" :key="s">{{ s }}</span>

    <div class="button-container">
      <button v-if="portfolio.portfolio.github" class="btn btn-outline-secondary" @click="open('github')">
        GitHub
        <i class="fa-brands fa-github"></i>
      </button>
      <button v-if="portfolio.portfolio.youtube" class="btn btn-outline-secondary" @click="open('youtube')">
        Demo Video
        <i class="fa-brands fa-youtube"></i>
      </button>
      <button v-if="portfolio.portfolio.demo" @click="navigateToDemo" class="btn btn-outline-secondary">
        Test
        <i class="fa-solid fa-laptop-code"></i>
      </button>
      <button v-if="portfolio.portfolio.contact" @click="open('contact')" class="btn btn-outline-secondary">
        Contact for Code
        <i class="fa-regular fa-envelope"></i>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CardItem',
  props: {
    portfolio: {
      type: Object,
      required: true,
    }
  },
  methods: {
    open(platform) {
      let email, subject, body;
      let url = '';
      switch (platform) {
        case 'github':
          url = this.portfolio.portfolio.github;
          break;
        case 'youtube':
          url = this.portfolio.portfolio.youtube;
          break;
        case 'demo':
          url = this.portfolio.portfolio.demo;
          break;
        case 'contact':
          email = 'darrison@andrew.cmu.edu';
          subject = '';
          body = '';
          url = `mailto:${email}?subject=${encodeURIComponent(subject)}&body=${encodeURIComponent(body)}`;
          break;
      }
      window.open(url, '_blank');
    },
    navigateToDemo() {
      this.$router.push(this.portfolio.portfolio.demo);
    }
  }
}
</script>

